  async setProfile({ commit }) {
    const owner = JSON.parse(sessionStorage.getItem("owner"));
    let id = await client.sendMessage({
      path: PRIVATE_POST_USER,
      node_id: owner.node_id,
      timestamp: new Date().toISOString(),
      body: {
        user_id: owner.user_id,
      },
    })
    const profile = await client.getResponse(id)
    commit("PROFILE_SET", profile);
  },

