export default {
  PROFILE_SET(state, profile) {
    state.profile = profile;
  },
  WARPNET_TWEETS(state, { cursor, tweets, user_id }) {
    console.log("WARPNET_TWEETS state", cursor, tweets, user_id)
    state.tweets.tweets = tweets;
    state.tweets.cursor = cursor;

  },
  WARPNET_TIMELINE(state, { cursor, tweets, user_id }) {
    console.log("WARPNET_TIMELINE state", cursor, tweets, user_id)
    state.timeline.tweets = tweets;
    state.timeline.cursor = cursor;

  },
  WARPNET_CREATE(state, newTweet) {
    const tweets = [...state.tweets.tweets];
    const timeline = [...state.timeline.tweets];
    state.tweets.tweets = tweets.unshift(newTweet);
    state.timeline.tweets = timeline.unshift(newTweet);
  },
  WARPNET_DELETE(state, tweet_id) {
    const tweets = [...state.tweets.tweets];
    const timeline = [...state.timeline.tweets];
    state.tweets.tweets = tweets.filter(tweet => tweet.id !== tweet_id);
    state.timeline.tweets = timeline.filter(tweet => tweet.id !== tweet_id);
  },
  WARPNET_REPLY_ADD(state, {rootId, newReply}) {
    const repliesMap = state.replies.replies;
    const replies = repliesMap.get(rootId);
    repliesMap.set(rootId, replies.unshift(newReply));
    state.replies.replies = repliesMap
  },
  WARPNET_LIKES_ADD(state, {tweetId, count}) {
    const likesMap = state.likes.likes;
    likesMap.set(tweetId, count);
    state.likes.likes = likesMap
  },
  WARPNET_RETWEETS_ADD(state, {tweetId, count}) {
    const retweetsMap = state.retweets.retweets;
    retweetsMap.set(tweetId, count);
    state.retweets.retweets = retweetsMap
  },
  WARPNET_WHO_TO_FOLLOW(state, { cursor, users }) {
    state.who_to_follow.users = users;
    state.who_to_follow.cursor = cursor;
  },
  WARPNET_FOLLOWERS(state, { cursor, followee, followers }) {
    state.followers.followers = followers;
    state.followers.cursor = cursor;
    state.followers.user_id = followee;
  },
  WARPNET_FOLLOWING(state, { cursor, follower, followees }) {
    state.following.following = followees;
    state.following.cursor = cursor;
    state.followers.user_id = follower;
  },

  WARPNET_LOADMORE_TIMELINE(state, {  cursor, tweets, user_id }) {
    state.timeline.tweets = [...state.timeline.tweets, ...tweets];
    state.timeline.cursor = cursor;
  },

  WARPNET_LOADMORE_TWEETS(state, {  cursor, tweets, user_id }) {
    state.tweets.tweets = [...state.tweets.tweets, ...tweets];
    state.tweets.cursor = cursor;
  },

  WARPNET_SEARCH(state, { results, cursor }) {
    state.search.results = results;
    state.search.cursor = cursor;
  },
  WARPNET_LOADMORE_SEARCH(state, { results, cursor }) {
    state.search.results = [...state.search.results, ...results];
    state.search.cursor = cursor;
  },

  WARPNET_SEARCH_HASHTAG(state, { results, cursor }) {
    state.search.results = results;
    state.search.cursor = cursor;
  },
  WARPNET_LOADMORE_SEARCH_HASHTAG(state, { results, cursor }) {
    state.search.results = [...state.search.results, ...results];
    state.search.cursor = cursor;
  },

  WARPNET_NOTIFICATIONS_SUBSCRIBE(state, subscription) {
    state.notifications.subscription = subscription;
    state.notifications.newNotifications = 0;
    state.notifications.messages.newMessages = 0;
  },
  WARPNET_NOTIFICATIONS_UNSUBSCRIBE(state) {
    let subscription = state.notifications.subscription;
    if (subscription) {
      subscription.unsubscribe();
      state.notifications.subscription = undefined;
    }
  },
  WARPNET_NOTIFICATIONS_NEW(state, newMessage) {
    if (newMessage?.type === "Mentioned") {
      state.notifications.mentions = [
        newMessage,
        ...state.notifications.mentions,
      ]; // reverse add order
    } else {
      state.notifications.all = [newMessage, ...state.notifications.all]; // reverse add order
    }
    state.notifications.newNotifications =
      state.notifications.newNotifications + 1;
  },
  WARPNET_NOTIFICATIONS_RESET(state) {
    state.notifications.newNotifications = 0;
  },
  WARPNET_CONVERSATIONS_LOAD(state, { conversations, cursor }) {
    state.notifications.messages.conversations = conversations;
    state.notifications.messages.cursor = cursor;
  },
  WARPNET_MESSAGES_NEW(state, notification) {
    let set = state.notifications.messages.conversationsSet;
    let c = state.notifications.messages.active.conversation;
    const conversationId =
      state.profile.id < notification.otherUserId
        ? `${state.profile.id}_${notification.otherUserId}`
        : `${notification.otherUserId}_${state.profile.id}`;

    // if conversation is active we ignore this
    const notActive = (c) => !c || c.id !== conversationId;
    if (notActive(c)) {
      if (!set.has(conversationId)) {
        set.add(conversationId);
      }
      state.notifications.messages.newMessages =
        state.notifications.messages.newMessages + 1;
      // trigger getters
      state.notifications.messages.conversations = [
        ...state.notifications.messages.conversations,
      ];
    }
  },
  WARPNET_MESSAGES_RESET(state) {
    state.notifications.messages.newMessages = 0;
  },
  WARPNET_MESSAGES_LOAD(state, { messages, cursor }) {
    state.notifications.messages.active.messages = messages;
    state.notifications.messages.active.cursorMessages = cursor;
  },
  WARPNET_LOADMORE_MESSAGES(state, { messages, cursor }) {
    state.notifications.messages.active.messages = [
      ...state.notifications.messages.active.messages,
      ...messages,
    ];
    state.notifications.messages.active.cursorMessages = cursor;
  },
  WARPNET_MESSAGES_SEND(state, newMessage) {
    state.notifications.messages.active.messages = [
      ...state.notifications.messages.active.messages,
      newMessage,
    ];
  },
  WARPNET_CONVERSATION_ACTIVE_SET(state, conversation) {
    let set = state.notifications.messages.conversationsSet;
    if (set && conversation && set.has(conversation.id)) {
      set.delete(conversation.id);
      // we had a hit enough to clear all notifications in this implementation
      // we still can show there's messages for other conversations with new messages just not how many messages
      state.notifications.messages.newMessages = 0;
    }
    state.notifications.messages.conversationsSet = set;
    state.notifications.messages.active.conversation = conversation;
    state.notifications.messages.active.messages = [];
    state.notifications.messages.active.cursorMessages = undefined;
  },
  WARPNET_RESET_STATE(state) {
    Object.assign(state, {
      profile: {
        id: "",
        created_at: "1970-01-01",
        avatar: "default_profile.png",
      },
      tweets: {
        tweets: [],
        cursor: undefined,
      },
      followers: {
        followers: [],
        cursor: undefined,
      },
      following: {
        following: [],
        cursor: undefined,
      },
      search: {
        results: [],
        cursor: undefined,
      },
      notifications: {
        all: [],
        mentions: [],
        newNotifications: 0,
        subscription: undefined,
        messages: {
          conversations: [],
          cursor: undefined,
          newMessages: 0,
          conversationsSet: new Set(),
          active: {
            conversation: undefined,
            messages: [],
            cursorMessages: undefined,
          },
        },
      },
    });
  },
};
